import { Component } from '@angular/core';
import { IonicPage, NavController, NavParams, AlertController } from 'ionic-angular';
import { ApiProvider } from '../../providers/api/api';
import { ViewController } from 'ionic-angular';
/**
 * Generated class for the CongratsPage page.
 *
 * See https://ionicframework.com/docs/components/#navigation for more info on
 * Ionic pages and navigation.
 */

@IonicPage()
@Component({
  selector: 'page-contact',
  templateUrl: 'contact.html',
})
export class ContactPage {
  public field1: string;
  public field2: string;

  constructor( public viewCtrl : ViewController,public apiProvider: ApiProvider,public navCtrl: NavController, public navParams: NavParams, public alertCtrl: AlertController) {

  }

  ionViewDidLoad() {
    
  }


  sendContact(){	
    this.apiProvider.enviarContacto({
      field1: this.field1,
      field2: this.field2
    }).then(() => {
      let alert = this.alertCtrl.create({
        title: 'Gracias por su mensaje',
        subTitle: '' ,
        buttons: ['Cerrar']
        });
        alert.present();
    })  
    this.closeModals();
}


public closeModals(){
    this.viewCtrl.dismiss();
}



}



